<template>

    <div class="histoire">

        <h1>La révolution</h1>

        Les évènements qui ont conduit à la Révolution française sont de nature diverse, à la fois sociale, économique
        et politique. L’Ancien Régime est en crise, la dette publique atteint des sommets et la société d’ordres est de
        plus en plus critiquée. Malgré la décision de Louis XVI de doubler le nombre des députés du Tiers État fin 1788,
        ces derniers demeurent minoritaires face à la noblesse et au clergé. La révolte gronde. "L’entrée en révolution
        se fait au printemps et dans l’été 1789 avec la transformation des Etats généraux en assemblée nationale le 17
        juin et l’affirmation de la souveraineté nationale", explique Hervé Leuwers.

        Avec le serment du Jeu de Paume le 20 juin, la nouvelle assemblée nationale s’engage à ne pas se séparer avant
        d’avoir rédigé la Constitution. "Le 9 juillet, les députés se déclarent Assemblée nationale constituante et la
        prise de la Bastille le 14 juillet, sauve la révolution d’un coup de force, alors que le roi menace de faire
        intervenir les troupes dans la capitale", détaille l’historien.

    </div>

</template>
    
<script>

export default {

    name: 'histoire',

    metaInfo: {

        title: 'Petite histoire de la révolution française.',

        meta: [{

            name: 'description',

            content: 'Devenez incollable sur la révolution française'

        }]

    }

}

</script>


