<template>

    <div class="culture">

        <h1>La culture française - L'homme qui rit de Victor Hugo</h1>

        Dès 1861 - 1862, Victor Hugo a le projet d'écrire une trilogie politique : un livre traitant de l'aristocratie
        (L'Homme qui rit), un autre traitant de la monarchie et le dernier traitant de la révolution
        (Quatrevingt-treize). On a la trace de ce projet par des notes prises par Victor Hugo dans les années 1862,
        1863, concernant l'étude d'une Chronique de la régence et du règne de Louis XV ou Journal de Barbier. Ce fait
        est confirmé par l'introduction de L'homme qui rit :

        « Le vrai titre de ce livre serait l'Aristocratie. Un autre livre, qui suivra, pourra être intitulé la
        Monarchie. Et ces deux livres, s'il est donné à l'auteur d'achever ce travail, en précèderont et en amèneront un
        autre qui sera intitulé : Quatrevingt-treize. »

    </div>

</template>
    
<script>

export default {

    name: 'culture',

    metaInfo: {

        title: "Introduction à l'homme qui rit",

        meta: [{

            name: 'description',

            content: "Découvrez l'homme qui rit, ouvrage de Victor Hugo."

        }]

    }

}

</script>


