<template>

    <div class="geographie">

        <h1>La géographie française</h1>

        La France métropolitaine a cinq façades maritimes sur (du nord au sud) : la mer du Nord, la Manche, la mer
        d'Iroise, l'océan Atlantique et la mer Méditerranée. Ainsi, la longueur totale de ses côtes atteint 3 427 km.

    </div>

</template>
    
<script>

export default {

    name: 'geographie',

    metaInfo: {

        title: 'Description générale de la géographie française',

        meta: [{

            name: 'description',

            content: 'Renseignez-vous sur la géographie française.'

        }]

    }

}

</script>


